<template>
    <div class="role-centainer">
        <el-card style="min-height:600px;">
            <el-row>
                <el-col :span="2">
                    <el-button type="primary" @click="goToAddRole" size="small"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="danger" plain @click="goToBatchForbidden" size="small"></el-button>
                </el-col>
                <el-col :span="10">
                    <el-button type="danger" plain @click="goToBatchDelete" size="small"></el-button>
                </el-col>
                <el-col :span="8">
                    <el-input placeholder="" v-model="searchVal" size="small" clearable @clear="clearInput"></el-input>
                </el-col>
                <el-col :span="2">
                    <el-button icon="el-icon-search" type="primary" @click="goToSearch" size="small"></el-button>
                </el-col>
            </el-row>
            <el-row>
                <el-table
                    ref="multipleTable"
                    :data="userList"
                    border
                    :header-cell-style="{background:'#EEEEEE',color:'#323232'}"
                    tooltip-effect="dark"
                    @selection-change="tableSelectionChange"
                    style="width: 100%">
                    <el-table-column
                    type="selection"
                    width="55">
                    </el-table-column>
                    <el-table-column
                    prop="name"
                    label=""
                    >
                    </el-table-column>

                    <el-table-column
                    label=""
                    show-overflow-tooltip>
                        <template slot-scope="scope">
                            <template  >{{ scope.row.roleInfos.join("") }}</template>
                        </template>
                    </el-table-column>
                    <el-table-column
                    label=""
                    >
                        <template slot-scope="scope" >
                            <template >{{scope.row.areas.join("")}}</template>

                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="operTime"
                    label=""
                    show-overflow-tooltip>
                    <template slot-scope="scope" >{{ formatDateChar(scope.row.roleInfo[0].created_at*1000) }}</template>
                    </el-table-column>
                    <el-table-column
                    label=""
                    >
                    <template slot-scope="scope">{{ scope.row.mobile }}</template>
                    </el-table-column>
                    <el-table-column
                    fixed="right"
                    label=""
                    width="150">
                    <template slot-scope="scope">
                        <template v-if="scope.row.status==1">
                            <el-button @click="goToEdit(scope.row.id)" type="text" size="small"></el-button>
                            <el-button @click="goToForbidden(scope.row.id)" type="text" size="small" style="color:red;"></el-button>
                        </template>
                        <template v-if="scope.row.status==0">
                            <el-button type="text" size="small" style="color:#999;" ></el-button>
                            <el-button @click="goToRelieve(scope.row.id)" type="text" size="small"></el-button>
                        </template>
                        <el-button @click="goToDelete(scope.row.id)" type="text" size="small" style="color:red;"></el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row class="page-div">
                <el-pagination
                @size-change="pageSizeChange"
                @current-change="pageCurrentChange"
                :current-page="currentPage"
                :page-sizes="[20, 30, 40, 50, 100]"
                :page-size="perPage"
                layout="total, sizes, prev, pager, next, jumper"
                :total="totalCount">
                </el-pagination>
            </el-row>
  
        </el-card>
    </div>
</template>
<script>
    import api from '../../service/api'
    export default{
        data(){
            return{
                searchVal:'',//
                userList: [],
                multipleSelection: [],
                currentPage:1,//
                perPage:20,//
                totalCount:0,//
            }
        },
        mounted(){
            let params={
                'per-page':this.perPage
            }
            params.page=this.currentPage
            params.role_id='1,3,4,5'
            params.expand='roleInfo'
            this.getUserList(params)
        },
        methods:{
            //
            async getUserList(params){
                if(this.searchVal!=''){
                    params.name=this.searchVal
                }
                params.url=api.user
                params.role_id='1,3,4'
                params.expand='roleInfo'
                let res=await this.axiosGet(params).catch(err=>err)
                this.userList=res.items
                this.totalCount=res._meta.totalCount
                for(let i in this.userList){
                    let areas=[]
                    let roleInfos=[]
                    for(let j in this.userList[i].roleInfo){
                        roleInfos.push(this.userList[i].roleInfo[j].name);
                        if(this.userList[i].roleInfo[j].areas!=undefined){
                            for(let k in this.userList[i].roleInfo[j].areas){
                                areas.push(this.userList[i].roleInfo[j].areas[k].name)
                            }
                        }  
                    }
                    this.userList[i].roleInfos=roleInfos
                    this.userList[i].areas=areas
                }
            },
            //
            goToAddRole(){
                this.$router.push({
                    path: '/addRole',
                })   
            },
            //
            goToSearch(){
                let params={
                    'per-page':this.perPage
                }
                params.page=this.currentPage
                this.getUserList(params)
            },
            clearInput(){
                console.log("")
                let params={
                    'per-page':this.perPage
                }
                params.page=this.currentPage
                this.getUserList(params)
            },
            //
            goToEdit(id){
                this.$router.push({
                    path:'/addRole',
                    query: {
                        id: id
                    }
                })
            },
            //
            goToForbidden(id){
                let status=0
                this.open(id,status)
            },
            //
            goToDelete(id){
                let status=-1
                this.open(id,status)
            },
            //
            open(id,status) {
                let tag=''
                if(status==0){
                    tag=''
                }else if(status==-1){
                    tag=''
                }
                this.$confirm(`${tag}, ?`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let params={}
                    params.id=id
                    params.status=status
                    this.updateUser(params) 
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                });
            },
            //
            async updateUser(params){
                params.url=api.user
                let msg=''
                if(params.status==0){
                    msg=''
                }else if(params.status==-1){
                    msg=''
                }
                await this.axiosPut(params).then(res=>{
                    this.$message({
                        type: 'success',
                        message: msg
                    });
                    let params={
                        'per-page':this.perPage
                    }
                    params.page=this.currentPage
                    this.getUserList(params)
                }).catch(err => err);
                
            },
            //
            async goToRelieve(id){
                let params={}
                params.id=id
                params.status=1
                params.url=api.user
                await this.axiosPut(params).then(res=>{
                    this.$message({
                        type: 'success',
                        message: ''
                    });
                    params={
                        'per-page':this.perPage
                    }
                    params.page=this.currentPage
                    this.getUserList(params)
                }).catch(err => err);
            },
            //
            tableSelectionChange(val){
                this.multipleSelection = val;
            },
            
            //
            pageSizeChange(val){
                this.perPage=val
                let params={
                    'per-page':this.perPage
                }
                params.page=this.currentPage
                this.getUserList(params)
            },
            
            //
            pageCurrentChange(val){
                this.currentPage=val
                let params={
                    'per-page':this.perPage
                }
                params.page=this.currentPage
                this.getUserList(params)
            },
            //
            async goToBatchForbidden(){
                let ids=[]
                for(let i in this.multipleSelection){
                    ids.push(this.multipleSelection[i].id)
                }
                if(ids.length==0){
                    this.$message({
                        type: 'info',
                        message: ''
                    })
                    return;
                }
                let params={}
                params.ids=ids
                params.url=api.disableUsers
                this.$confirm(`, ?`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                     this.batchForbidden(params)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                });
            },
            //
            async batchForbidden(params){
                await this.axiosPost(params).then(res=>{
                    this.$message({
                        type: 'success',
                        message: ''
                    });
                    let params={
                        'per-page':this.perPage
                    }
                    params.page=this.currentPage
                    this.getUserList(params) 
                }).catch(err=>err)
            },
            //
            goToBatchDelete(){
                let ids=[]
                for(let i in this.multipleSelection){
                    ids.push(this.multipleSelection[i].id)
                }
                if(ids.length==0){
                    this.$message({
                        type: 'info',
                        message: ''
                    })
                    return;
                }
                let params={}
                params.ids=ids
                params.url=api.deleteUsers
                this.$confirm(`, ?`, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.batchDelete(params)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });          
                });
            },
            //
            async batchDelete(params){
                await this.axiosPost(params).then(res=>{
                    this.$message({
                        type: 'success',
                        message: ''
                    });
                    let params={
                        'per-page':this.perPage
                    }
                    params.page=this.currentPage
                    this.getUserList(params) 
                }).catch(err=>err)
            }
            
        }
    }
</script>
<style lang="less" scoped>
.role-centainer{
    width:1180px;
    margin:auto;
    margin-top:20px;
    .page-div{
        display:flex;
        justify-content:center;
    }
}
</style>