<template>
  <div class="video-player">
    <video-player
      class="video-player vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
      @play="onPlayerPlay($event)"
      @pause="onPlayerPause($event)"
    ></video-player>
  </div>
</template>
<script>
export default {
  props: {
    videoSrc: {
      type: String,
      default() {
        return "";
      }
    }
  },
  data() {
    return {
      playerOptions: {
        //
        playbackRates: [0.5, 1.0, 1.5, 2.0],
        //true,
        autoplay: false,
        // 
        muted: false,
        // 
        loop: false,
        // <video>auto,
        preload: "auto",
        language: "zh-CN",
        //  - "16:9""4:3"
        aspectRatio: "16:9",
        // trueVideo.js player
        fluid: true,
        sources: [
          {
            //
            type: "video/mp4",
            //url
            src: ""
          }
        ],
        //
        poster: "",
        //Video.js
        notSupportedMessage: "",
        controlBar: {
          timeDivider: false,
          durationDisplay: false,
          remainingTimeDisplay: false,
          //
          fullscreenToggle: true
        }
      }
    };
  },
  mounted() {
    this.playerOptions.sources[0].src = this.videoSrc;
  },
  watch: {
    // videoUrl
    videoSrc(val) {
      if (val !== "") {
        this.$refs.videoPlayer.player.src(val);
      }
    }
  },

  methods: {
    //
    onPlayerPlay(player) {
      this.$refs.videoPlayer.player.play();
    },
    //
    onPlayerPause(player) {
      this.$refs.videoPlayer.player.pause();
    }
  },
  computed: {
   
  }
};
</script>
<style lang="less">
.video-player {
  display: inline-block;
  width: 750px;
  height: 430px;
  text-align: center;
  line-height: 100px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  margin-right: 4px;
}
.video-js .vjs-big-play-button {
  width: 2em !important;
  border-radius: 1em !important;
  margin-left: -1em !important;
}
.video-player:hover {
  display: block;
}
</style>