<template>
  <div class="activity-work-show">
    <template v-if="workList.length > 0">
      <el-card class="work-list-item" v-for="(item, index) in workList" :key="index">
        <div class="work-list-top">
          <div class="picleft">
              <img :src="item.url" > 
          </div>
          <div class="jianjie">
            <div class="name"><p>{{item.name}}</p></div>
            <div class="activeclass">
              <p >{{item.avitveTiem}}</p>
              <p >{{item.activeObject[0]}} </p>
              <p >{{item.activeGroup[0]}} </p>
              <p >{{item.activeLimit[0]}} </p>
              <p >{{item.activeClass[0]}} </p>
              <div class="activeclass-bottom">
                <el-button type="primary" @click="goToActivityEnroll"></el-button>
              </div>
              
            </div>
          </div>
        </div>
        <!---->
        <div class="count">
            <p class="title"></p>
            <hr style="background:rgba(229,229,229,1);" />
            <div class="count-item">
              <p class="sontitle"></p>
              <p class="soncount">{{item.activePreface}}</p>
            </div>
            <div class="count-item">
              <p class="sontitle"></p>
              <p class="soncount">{{item.activeAddr}}</p>
            </div>
            <div class="count-item">
              <p class="sontitle"></p>
              <p class="soncount">{{item.activeRequer}}</p>
            </div>
            
            <hr style="background:rgba(229,229,229,1);" />
        </div>
       
      </el-card>
    </template>
    <div v-else class="no-data"></div>
  </div>
</template>
<script>
  import ActivityList from '../../components/ActivityList';
  export default {
    name: 'activity-work-show',
    components: { ActivityList },
    data() {
      return {
        workList: [],
        activityId: ''
      };
    },
    props: {
      stage: { type: String }
    },
    mounted() {
      this.activityId = this.$route.query.ActivityId;
      if (this.stage == 'STAGE_WORK_SHOW') this.getAllWorkList();
      else if (this.stage == 'STAGE_EXCELLENT_WORK_SHOW') this.getExcellentWorkList();
    },
    methods: {
      //
      goToActivityEnroll(){
        this.$router.push({
          path: '/home/activityEnroll',
        });
      },
      async getAllWorkList() {
        // let res = await this.axiosGet({
        //   url: '/v1/activity/GetWorkList',
        //   ActivityId: this.activityId,
        //   Category: 0
        // }).catch(err => err);
        // if (res.ErrorCode != 'OK') return;
        // let list = res.Data.WorkList || [];
        // this.workList = list.map(item => {
        //   let resourceList = item.ResourceList;
        //   let video = resourceList.find(re => re.ResourceType == 4);
        //   return {
        //     name: item.TeamName,
        //     school: item.School,
        //     url: video.ResourceContent
        //   };
        // });
        this.workList=[
          {url:require('../../assets/img2/1.png'),
          name:'',
          avitveTiem:'2020-01-05  2020-02-06',
          activeObject:[''],
          activeGroup:['',''],
          activeLimit:['','','',''],
          activeClass:[''],
          activePreface:'2019',
          activeAddr:'PPT2019http://cxyy.szdj.edu.cn 2019108  1018 1025 ',
          activeRequer:'20 20 1',
          }
          ]
       
      },
      async getExcellentWorkList() {
        let res = await this.axiosGet({
          url: '/v1/activity/GetExcellentWorkList',
          ActivityId: this.activityId,
          GradeId: 5
        }).catch(err => err);
        if (res.ErrorCode != 'OK') return;
        let list = res.Data.WorkList || [];
        this.workList = list.map(item => {
          let resourceList = item.ResourceList;
          let video = resourceList.find(re => re.ResourceType == 4);
          return {
            name: item.TeamName,
            school: item.School,
            url: video.ResourceContent
          };
        });
      }
    }
  };
</script>
<style lang="less">
  p{
    margin: 0;
    padding: 0;
  }
  .activity-work-show { 
    .work-list-item {
      width:1180px;
      display: inline-block;
      padding: 30px;
      .work-list-top{
        display: flex;
        flex-direction: row;
      }
      .picleft{
          img{
          width:360px; 
          height:270px;
          border-radius:8px;
        }
      }
      .jianjie{
          margin-left: 60px;
          height: 270px;
          .name{
            width:658px;
            background:rgba(244,247,249,1);
            p{
              font-size:24px;
              color:#0386FF;
              padding: 10px;
            }
          }
          .activeclass{
            display:flex;
            flex-direction: column;
            justify-content: start;
            p{
              padding: 5px 10px;
            }
            .activeclass-bottom{
              padding-left: 10px;
              margin-top: 20px;
            }
          }
 
      }
      .count{ 
        .title{
          font-size:16px;
          font-family:MicrosoftYaHei;
          color:rgba(102,102,102,1);
          line-height:43px;
          margin: 20px 0;
        }
        .count-item{
          margin: 20px 30px;
          .sontitle{
            font-size:16px;
            font-family:Microsoft YaHei;
            font-weight:400;
            color:#333;
            line-height:43px;
          }
        .soncount{
            text-indent:2em;
            font-size:14px;
            font-family:Microsoft YaHei;
            font-weight:400;
            color:#333;
            line-height:30px;
          }
        }  
      }
      .school {
        font-size: 14px;
        color: #999999;
      }
      video {
        width: 100%;
      }
    }
    .no-data {
      text-align: center;
      color: #999999;
    }
  }
</style>