    },
        mounted() {
     
      let list=[{Name:'',Value:320},{Name:'',Value:360},{Name:'',Value:520},{Name:'',Value:330},{Name:'',Value:380},{Name:'',Value:320},{Name:'',Value:360},{Name:'',Value:520},{Name:'',Value:330},{Name:'',Value:380}]
      this.$nextTick(function(){
        this.renderChart(list)
      })
      
    },
    methods: {
       renderChart(list = []) {
        let xAxis = [], data = [];
        list.forEach(item => {
          xAxis.push(item.Name);
          data.push(item.Value);
        });
        let options = {
          show: true,
          chart: {
            // polar: true,
            type: 'column'
          },
          title: {
            text: ''
          },
          pane: {
            size: '25%'
          },
          xAxis: {
            categories: xAxis,
           
          },
          yAxis: {
            title: {
              text: ''
            },
           
            min: 0
          },
          legend: {
            enabled: false
          },
       
          series: [{
            name: '',
            data: data,
            dataLabels: {
              enabled: true,
              // rotation: 90,
              color: '#333333',
              align: 'center',
              // y: 10
            },
          }]
        };
        this.enrollSta = options;
      },

