<template>
    <div class="home-center">
        <el-card style="min-height:650px;">
            <div class="home-wrap">
                <!-- <div>
                    <type-select :gradeList="gradeList" :activityObjectList="activityObjectList" :regionList="regionList" @gradeObject='gradeObject' 
                    @regionObject="regionObject" @activityObject="activityObject" ></type-select>
                </div> -->
                <div class="home-activity-list" v-if="activityList">
                    <card-list :cardList="list" @toNext="toNext"></card-list>
                </div>
            </div> 
        </el-card> 
	</div>
</template>
<script>
import CardList from '../components/CardList';
import MyVideoPlayer from '../components/MyVideoPlayer';
import UploadPicture from '../components/UploadPicture';
import UploadFile from '../components/UploadFile';
import FilePreview from '../components/FilePreview';
import TypeSelect from '../components/TypeSelect';
import api from '../service/api';
export default {
    name: 'home-center',
    components: { CardList ,MyVideoPlayer,FilePreview,UploadFile,TypeSelect,UploadPicture},
    data() {
        return {
            
            activityObjectList:[
                {id:0,name:''},
                {id:1,name:''},
                {id:2,name:''},
                {id:4,name:''}
            ],
            regionList:[
               
                    {"id":"77","pid":"6","type":"2","name":""},
                    {"id":"726","pid":"77","type":"3","name":""},
                    {"id":"727","pid":"77","type":"3","name":""},
                    {"id":"728","pid":"77","type":"3","name":""},
                    {"id":"729","pid":"77","type":"3","name":""},
                    {"id":"730","pid":"77","type":"3","name":""},
                    {"id":"731","pid":"77","type":"3","name":""},
                    {"id":"732","pid":"77","type":"3","name":""},
                    {"id":"733","pid":"77","type":"3","name":""},
                    {"id":"734","pid":"77","type":"3","name":""},
                    {"id":"735","pid":"77","type":"3","name":""},
                    {"id":"736","pid":"77","type":"3","name":""}
                
            ],
            activityList: [
                { id: 1, name: '', imgUrl: require('../public/images/ac1.png') }, 
                { id: 2, name: '', imgUrl: require('../public/images/ac2.png') }, 
                { id: 3, name: '', imgUrl: require('../public/images/ac3.png') }, 
                { id: 4, name: '', imgUrl: require('../public/images/ac4.png') }, 
                { id: 5, name: '', imgUrl: require('../public/images/ac5.png') }, 
                { id: 6, name: 'AI', imgUrl: require('../public/images/ac6.png') }
            ], // 
            // gradeList:[
            //     { id: 0, name: '' },
            //     { id: 1, name: '' },
            //     { id: 2, name: '' },
            //     { id: 3, name: '' }
            // ],
            list:[],
            gradeList:[
            { id: 0, name: '' },
            {name:"",id:1},
            {name:"",id:2},
            {name:"",id:4},
            {name:"",id:8},
            {name:"",id:16},
            {name:"",id:32},
            {name:"",id:64},
        ],
        activityObjectid:0,
        gradeObjectid:0,
        regionObjectid:'77',
        };
        
    },
    created(){
        this.selectActive()
    },
    methods: {
        //
        async selectActive(){
            let params={}
            params.url=api.activityDetail
            params.process=1;
            if(this.activityObjectid!=0){
                params.target=this.activityObjectid;
            }
            if(this.gradeObjectid!=0){
                params.period=this.gradeObjectid;
            }
            if(this.regionObjectid!="77"){
                params.region=this.regionObjectid;
            }
            let res = await this.axiosGet(params).catch(err => err);
            this.list=res.items
        },

        // 
        toNext(id){
            this.$router.push({
                path: '/home/activityIntroduction',
                query: {
                    id: id,
                    activeName:'first'
                }
            });
        },
        //
        async activityObject(value){
            this.activityObjectid=value
            this.selectActive();
           
        },
        //
        async gradeObject(value) {
            this.gradeObjectid=value
             this.selectActive();
        },
        //
        async regionObject(value){
             this.regionObjectid=value
            this.selectActive();
        }
    }
};
</script>
<style lang="less" scoped>
.home-center{
    width: 1180px;
    margin: auto;
    margin-top: 20px;
    .home-wrap{
        .home-header{
            width: 100%;
            height: 120px;
            font-size: 28px;
            line-height: 120px;
            text-align: center;
            color: #fff;
            background: url('../public/images/banner.png');
            background-size: cover;
            border-radius: 4px;
            margin-bottom: 20px;
        }
        .home-activity-list{
            // margin-top:30px;
        }
        
    }
}

</style>
