<template>
    <div class="classify-centainer">
        <el-card style="min-height:600px;">
            <el-tabs v-model="activeName">
                <el-tab-pane label="" name="classify">
                    <el-row>
                        <el-col :span="3">
                            <el-button type="primary" @click="goToAddClassify" size="small"></el-button>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col>
                            <div class="card">
                                <template v-for="item in cardList">
                                    <card :cardDetail="item" @goToAdd="goToAdd" @goToEdit="goToEdit" @goToDelete="goToDelete"></card>
                                </template>
                            </div>
                        </el-col>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
        </el-card>
    </div>
</template>
<script>
    import Card from '../../components/Card'
    export default{
        components: { Card },
        data(){
            return{
                activeName:'classify',
                cardList:[
                    {id:1,imgUrl:require('../../public/images/ac1.png'),title:''},
                    {id:2,imgUrl:require('../../public/images/ac2.png'),title:''},
                    {id:3,imgUrl:require('../../public/images/ac3.png'),title:''},
                    {id:4,imgUrl:require('../../public/images/ac4.png'),title:''},
                    {id:5,imgUrl:require('../../public/images/ac5.png'),title:''},
                    {id:6,imgUrl:require('../../public/images/ac6.png'),title:'AI'}
                    
                ]
            }
        },
        mounted(){

        },
        methods:{
            //
            goToAddClassify(){
                this.$router.push({
                    path:'/addClassify'
                })
            },
             //
            goToAdd(val){
                console.log("id==",val)
                this.$router.push({
                    path:'/subClassify',
                    query: {
                        id: val.id,
                        title:val.title
                    }
                })
            },
            //
            goToEdit(val){
                console.log("id==",val)
            },
            //
            goToDelete(val){
                console.log("id==",val)
            }
        }
    }
</script>
<style lang="less" scoped>
.classify-centainer{
    width:1180px;
    margin:auto;
    margin-top:20px;
    .card{
        display:flex;
        flex-flow:row wrap;
    }
}
</style>