
<template>
  <div class="user-edit-center">
    <el-card>
      <div class="user-edit-wrap">
        <div class="user-edit-top">
          <p></p>
        </div>
        <div class="user-edit-body">
          <el-form ref="form" :model="form" label-width="100px">
            <el-form-item label="">
              <template>
                <div class="user-icom">
                  <el-image :src="form.userIcon" fit="cover" v-if="form.userIcon!=''"></el-image>
                  <el-image :src="userIcon" fit="cover" v-else></el-image>
                </div>
              </template>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="form.userName" size="small" style="width:215px"></el-input>
            </el-form-item>

            <el-form-item
              label=""
              v-if="nowRole.type == 11 ||nowRole.type == 12||nowRole.type == 13"
            >
              <el-select
                v-model="areaid"
                filterable
                placeholder=""
                size="small"
                @change="areaChange"
              >
                <el-option
                  v-for="item in areaList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="" v-else>
              <el-input disabled v-model="form.areaName" size="small" style="width:215px"></el-input>
            </el-form-item>

            <el-form-item
              label=""
              v-if="nowRole.type == 11 ||nowRole.type == 12||nowRole.type == 13"
            >
              <el-select
                v-model="school_id"
                filterable
                placeholder=""
                size="small"
                @change="schoolChange"
              >
                <el-option
                  v-for="item in schoolList"
                  :key="item.id"
                  :label="item.title"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="" v-else>
              <el-input disabled v-model="form.unitName" size="small" style="width:215px"></el-input>
            </el-form-item>

            <el-form-item label="" v-show="flag==2">
              <el-select
                v-model="period_id"
                filterable
                placeholder=""
                size="small"
                @change="periodChange"
              >
                <el-option
                  v-for="item in periodArray"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" v-show="flag==2">
              <el-select
                v-model="grade_id"
                filterable
                placeholder=""
                size="small"
                @change="gradeChange"
              >
                <el-option
                  v-for="item in gradeArray"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" v-show="flag==2">
              <el-select
                v-model="class_id"
                filterable
                placeholder=""
                size="small"
                @change="classChange"
              >
                <el-option
                  v-for="item in classArray"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="" v-show="flag==3">
              <el-select
                v-model="student_id"
                filterable
                placeholder=""
                size="small"
                @change="studentChange"
              >
                <el-option
                  v-for="item in studentArray"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="">
              <el-tag
                :key="index"
                v-for="(tag,index) in identityTags"
                :disable-transitions="false"
              >{{tag.name}}</el-tag>
            </el-form-item>

            <el-form-item v-show="flag!=10">
              <el-button type="primary" @click="onSubmit()" size="small"></el-button>
              <el-button size="small" @click="cancel()"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script>
import api from "../../service/api";
import UploadFile from "../../components/UploadFile";
import { setCookie, getCookie } from "../../tools/tools";
export default {
  components: { UploadFile },
  data() {
    return {
      form: {
        userIcon: "",
        userName: "",
        unitName: "",
        areaName: "",
        className: ""
      },
      school_id: "",
      period_id: "",
      grade_id: "",
      class_id: "",
      identityTags: [],
      flag: 0,
      userIcon: require("../../public/images/userIcon.png"),
      nowRole: JSON.parse(localStorage.getItem("nowRole")),
      schoolList: [], //
      areaid: "",
      areaList: [
        { id: 732, pid: "77", type: "3", name: "" },
        { id: 726, pid: "77", type: "3", name: "" },
        { id: 727, pid: "77", type: "3", name: "" },
        { id: 728, pid: "77", type: "3", name: "" },
        { id: 729, pid: "77", type: "3", name: "" },
        { id: 730, pid: "77", type: "3", name: "" },
        { id: 731, pid: "77", type: "3", name: "" },
        { id: 733, pid: "77", type: "3", name: "" },
        { id: 734, pid: "77", type: "3", name: "" },
        { id: 735, pid: "77", type: "3", name: "" },
        { id: 736, pid: "77", type: "3", name: "" },
        { id: 3789, pid: "77", type: "3", name: "" }
      ],
      userInfo: {}, //
      periodArray: [
        { name: "", id: 1 },
        { name: "", id: 2 },
        { name: "", id: 4 },
        { name: "", id: 8 },
        { name: "", id: 16 },
        { name: "", id: 32 },
        { name: "", id: 64 }
      ],
      gradeArray: [
        { name: "", id: 1 },
        { name: "", id: 2 },
        { name: "", id: 3 },
        { name: "", id: 4 },
        { name: "", id: 5 },
        { name: "", id: 6 }
      ],
      classArray: [
        { name: "1", id: 1 },
        { name: "2", id: 2 },
        { name: "3", id: 3 },
        { name: "4", id: 4 },
        { name: "5", id: 5 },
        { name: "6", id: 6 },
        { name: "7", id: 7 },
        { name: "8", id: 8 },
        { name: "9", id: 9 },
        { name: "10", id: 10 },
        { name: "11", id: 11 },
        { name: "12", id: 12 },
        { name: "13", id: 13 },
        { name: "14", id: 14 },
        { name: "15", id: 15 },
        { name: "16", id: 16 },
        { name: "17", id: 17 },
        { name: "18", id: 18 },
        { name: "19", id: 19 },
        { name: "20", id: 20 }
      ],
      studentArray: [], //
      student_id: "",
      memberTypeObj: {
        0: "",
        2: "",
        3: "",
        4: "",
        11: "",
        12: "",
        13: ""
      }
    };
  },
  created() {
    this.gitUserInfo();
  },
  methods: {
    //
    async gitUserInfo() {
      let that = this;
      let params = {};
      params.url = api.user;
      params.id = JSON.parse(localStorage.getItem("user")).id;
      params.expand = "memberAuth,memberInfo,roleInfo";
      await this.axiosGet(params)
        .then(res => {
          //
          that.userInfo = res;
          that.form.userIcon = res.avatar;
          that.form.userName = res.name;
          if (
            that.nowRole.type == 11 ||
            that.nowRole.type == 12 ||
            that.nowRole.type == 13
          ) {
            if (res.memberInfo[0].type == 1) {
              that.flag = 1; //
              that.form.unitName = res.memberInfo[0].school.title;
              that.form.areaName = res.memberInfo[0].school.areaName;
              that.identityTags = [{ name: "" }];
              that.school_id = res.memberInfo[0].school.id;
              that.areaid = res.memberInfo[0].school.areaid;
              let params = {
                "filter[areaid]": that.areaid
              };
              that.getSchoolList(params);
            } else if (res.memberInfo[0].type == 2) {
              that.flag = 2; //
              that.form.unitName = res.memberInfo[0].school.title;
              that.form.areaName = res.memberInfo[0].school.areaName;
              that.form.className =
                res.memberInfo[0].school.classes[0].class_alias;
              that.period_id = res.memberInfo[0].school.classes[0].period;
              that.grade_id = res.memberInfo[0].school.classes[0].grade;
              that.class_id = res.memberInfo[0].school.classes[0].class;
              that.identityTags = [{ name: "" }];
              that.school_id = res.memberInfo[0].school.id;
              that.areaid = res.memberInfo[0].school.areaid;
              let params = {
                "filter[areaid]": that.areaid
              };
              that.getSchoolList(params);
            } else if (res.memberInfo[0].type == 3) {
              that.flag = 3; //
              that.form.unitName = res.memberInfo[0].school.title;
              that.form.areaName = res.memberInfo[0].school.areaName;
              that.identityTags = [{ name: "" }];
              that.school_id = res.memberInfo[0].school.id;
              let params = {};
              params.school = that.school_id;
              that.getStudentList(params);
              that.areaid = res.memberInfo[0].school.areaid;
              params = {
                "filter[areaid]": that.areaid
              };
              that.getSchoolList(params);
              that.student_id = res.memberInfo[0].students[0].id;
            }
          } else {
            //
            that.flag = 10;
            that.form.unitName = "";
            that.form.areaName = "";
            // this.form.unitName=res.memberInfo[0].school.areaName
            if (that.nowRole.type == 0) {
              that.identityTags = [{ name: "" }];
            } else if (that.nowRole.type == 2) {
              that.identityTags = [{ name: "" }];
            } else if (that.nowRole.type == 3) {
              that.identityTags = [{ name: "" }];
            } else if (that.nowRole.type == 4) {
              that.identityTags = [{ name: "" }];
            }
          }
        })
        .catch(err => err);
    },
    //
    async getSchoolList(params) {
      params.url = api.school;
      params.ball = 1;
      await this.axiosGet(params)
        .then(res => {
          this.schoolList = res.items;
        })
        .catch(err => err);
    },
    //
    areaChange(id) {
      let params = {
        "filter[areaid]": id
      };
      this.getSchoolList(params);
    },
    //
    schoolChange(id) {
      let that = this;
      that.student_id = "";
      let params = {};
      params.school = that.school_id;
      that.getStudentList(params);
    },
    //
    periodChange() {},
    //
    gradeChange() {},
    //
    classChange() {},
    //
    studentChange() {},
    //
    async getStudentList(params) {
      let that = this;
      params.expand = "memberAuth,memberInfo,roleInfo";
      params.ball = 1;
      params.mtype = 2;
      params.url = api.user;
      await this.axiosGet(params)
        .then(res => {
          let newStudentArray = [];
          if (res.items != undefined) {
            for (let i in res.items) {
              newStudentArray.push({
                id: res.items[i].id,
                name: res.items[i].name
              });
            }
            that.studentArray = newStudentArray;
          }
        })
        .catch(err => err);
    },
    //
    async onSubmit() {
      let that = this;
      let params = {};
      params.url = api.user;
      params.id = this.userInfo.id;
      params.name = this.form.userName;
      params.openid = this.userInfo.openid;
      params.mobile = this.userInfo.mobile;
      params.email = this.userInfo.email;
      params.avatar = this.userInfo.avatar;
      params.gender = this.userInfo.gender;
      params.birthday = this.userInfo.birthday;
      if (that.nowRole.type == 11) {
        params.identity = [
          {
            type: 1,
            school: this.school_id
          }
        ];
      } else if (that.nowRole.type == 12) {
        params.identity = [
          {
            type: 2,
            school: this.school_id,
            period: this.period_id,
            grade: this.grade_id,
            class: this.class_id
          }
        ];
      } else if (that.nowRole.type == 13) {
        params.identity = [
          {
            type: 3,
            children: [that.student_id]
          }
        ];
      }
      await this.axiosPut(params)
        .then(res => {
          if (res.id != undefined) {
            this.$message({
              type: "success",
              message: ""
            });
            this.getRoleInfo();
            setTimeout(() => {
              this.$router.go(-1);
            }, 1000);
          } else {
            this.$message({
              type: "warning",
              message: ""
            });
          }
        })
        .catch(err => err);
    },
    //
    cancel() {
      this.$router.go(-1);
    },
    //
    async getRoleInfo() {
      let that = this;
      let params = {};
      params.type = that.nowRole.type;
      params.url = api.switchToken;
      await this.axiosGet(params)
        .then(res => {
          if(res.token!=undefined){
            setCookie("x-api-key", res.token);
          }
          that.$store.dispatch("INIT_USER", res);
          let userInfo = JSON.stringify(res);
          localStorage.setItem("user", userInfo);
          let members = res.members.map(items => {
            let memberItem = {};
            memberItem.type = items.type;
            memberItem.name = that.memberTypeObj[items.type];
            if (items.school_id != undefined) {
              memberItem.school_id = items.school_id;
            }
            if (items.school_title != undefined) {
              memberItem.school_title = items.school_title;
            }
            if (items.current != undefined) {
              memberItem.current = items.current;
              let nowRole = JSON.stringify(memberItem);
              localStorage.setItem("nowRole", nowRole);
              this.$store.dispatch("INIT_NOWROLE", memberItem);
              console.log("nowrole===", nowRole);
            }
            return memberItem;
          });
          let roles = JSON.stringify(members);
          localStorage.setItem("roles", roles);
          that.$store.dispatch("INIT_ROLES", members);
        })
        .catch(err => err);
    }
  }
};
</script>
<style>
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
<style lang="less" scoped>
.user-edit-center {
  width: 1180px;
  margin: auto;
  margin-top: 20px;
  box-sizing: border-box;
  .user-edit-wrap {
    .user-edit-top {
      p {
        padding-bottom: 20px;
      }
      border-bottom: 1px solid #e5e5e5;
    }
    .user-edit-body {
      padding: 60px;
      width: 520px;
      .user-icom {
        display: flex;
        align-items: center;
        .el-image {
          height: 80px;
          width: 80px;
          border-radius: 8px;
        }
        .upload-icon {
          margin-left: 40px;
        }
      }
    }
  }
}
</style>